*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

img {
  max-width: 100%;
}

body {
  height: 100%;
  min-height: 100%;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;

  margin: 0;
}

.no-scroll {
  position: fixed;
  height: 100%;
  overflow: hidden;
}
